import {Avatar, Grid, List, ListItem, ListItemAvatar, ListItemText, Paper, Typography} from "@mui/material";
import {useSelector} from "react-redux";
import LockIcon from '@mui/icons-material/Lock';
import LockClockIcon from '@mui/icons-material/LockClock';
import LocalAtmIcon from '@mui/icons-material/LocalAtm';
import PercentIcon from '@mui/icons-material/Percent';
import {DivvyCryptoFormat} from "../utils";

export default function ProjectLocker(props) {

    const {project} = props;
    const {projectsMap} = useSelector(state => state.divvy);

    const {locker, stats} = projectsMap[project.contract];

    const dateLabel = new Date(Date.now() + (locker.time * 1000)).toLocaleDateString('en-us', {  year:"numeric", month:"short", day:"numeric"})

    return <Grid container spacing={2}>
        <Grid item xs={12}>
            <Paper style={{padding: 20, border: '1px solid #eee'}} elevation={0}>
                <Typography variant="h6">Divvy Locker</Typography>
                <Grid container>
                    <Grid item xs={12} md={3}>
                        <List disablePadding>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar sx={{bgcolor: '#872eff'}}><LockIcon/></Avatar>
                                </ListItemAvatar>
                                <ListItemText
                                    primary={`${DivvyCryptoFormat(locker.locked)} ${project.symbol}`}
                                    secondary="Locked Supply"/>
                            </ListItem>
                        </List>
                    </Grid>
                    <Grid item xs={12} md={3}>
                        <List disablePadding>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar sx={{bgcolor: '#872eff'}}><PercentIcon/></Avatar>
                                </ListItemAvatar>
                                <ListItemText
                                    primary={(locker.locked/stats.supply*100).toFixed(2)}
                                    secondary="Locked Percentage"/>
                            </ListItem>
                        </List>
                    </Grid>
                    <Grid item xs={12} md={3}>
                        <List disablePadding>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar sx={{bgcolor: '#872eff'}}><LocalAtmIcon/></Avatar>
                                </ListItemAvatar>
                                <ListItemText
                                    primary={`${DivvyCryptoFormat(locker.reflections)}  ${project.symbol}`}
                                    secondary="Available Reflections"/>
                            </ListItem>
                        </List>
                    </Grid>
                    <Grid item xs={12} md={3}>
                        <List disablePadding>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar sx={{bgcolor: '#872eff'}}><LockClockIcon/></Avatar>
                                </ListItemAvatar>
                                <ListItemText
                                    primary={dateLabel}
                                    secondary="Unlock Date"/>
                            </ListItem>
                        </List>
                    </Grid>
                </Grid>
            </Paper>
        </Grid>
    </Grid>;
}